/**  */
const getDaysOutpatientData = async() => {

  const date = `${trackState.year}-${trackState.month + 1 <= 9 ?'0':''}${trackState.month + 1}-${trackState.date <= 9 ?'0':''}${trackState.date}`;

  let url = `/api/Outpatient/GetDaysOutpatientData?departmentId=${trackState.department}&date=${date}${trackState.filterQueryStatus.morning ? `&clinicPeriods=Morning`: ''}${trackState.filterQueryStatus.afternoon ? `&clinicPeriods=Afternoon`: ''}${trackState.filterQueryStatus.evening ? `&clinicPeriods=Evening`: ''}`;

  trackState.selectedDoctor.forEach(item => {
    url = `${url}&doctors=${item.id}`;
  });

  const token = fetchFunction.getTokenFromCookie();

  const options = {
    headers: {
      'Content-Type': 'application/json',
      "Authorization" : `Bearer ${token}`
    },
    method: 'GET'
  }

  const response = await fetchFunction.fetchApi(url, options);

  trackState.daysOutpatientData = response.data;

  const { daysOutpatients, customerStoredValueData, voucherDepositData } = trackState.daysOutpatientData;

  trackState.customerStoredValueData = customerStoredValueData;
  trackState.voucherDepositData = voucherDepositData;
  
  // 

  const timeArr = Object.keys(daysOutpatients);

  if (timeArr.length === 0) return null;

  // 
  const hourIntervals = timeArr[1].split(':')[0] - timeArr[0].split(':')[0];
  const minuteIntervals = timeArr[1].split(':')[1] - timeArr[0].split(':')[1];

  const lastData = timeArr[timeArr.length - 1].split(':');

  let time = new Date(0, 0, 0, lastData[0], lastData[1]);

  time.setHours(time.getHours() + hourIntervals);
  time.setMinutes(time.getMinutes() + minuteIntervals);

  trackState.offWorkTime = `${time.getHours()}:${time.getMinutes() <= 9 ? 0 : ''}${time.getMinutes()}`;

  // 

  trackState.timeRange = [];

  if (trackState.department === 'AestheticMedicine') {
    for(let i = 0; i < timeArr.length; i++) {
      for(let j = 0; j < trackState.selectedDoctor.length; j++) {
        if(daysOutpatients[timeArr[i]][trackState.selectedDoctor[j].id].normalAppointment) {
          if(daysOutpatients[timeArr[i]][trackState.selectedDoctor[j].id].normalAppointment.timeRange > 1) {
  
            let itemRange = daysOutpatients[timeArr[i]][trackState.selectedDoctor[j].id].normalAppointment.timeRange;

            for(let k = i; k < (i + itemRange); k++) {
                trackState.timeRange.push({
                operatorId: daysOutpatients[timeArr[i]][trackState.selectedDoctor[j].id].normalAppointment.operatorId,
                time: timeArr[k],
                left: daysOutpatients[timeArr[i]][trackState.selectedDoctor[j].id].normalAppointment.left,
                lastTimeSlot: (k + 1) === (i + itemRange) ? true : false,
              })
            }
          }
        }
      }
    }
  }

  if (trackState.department === 'Beauty') {
    for(let i = 0; i < timeArr.length; i++) {
      if (daysOutpatients[timeArr[i]].normalAppointment) {
        if(daysOutpatients[timeArr[i]].normalAppointment.timeRange > 1) {
  
          let itemRange = daysOutpatients[timeArr[i]].normalAppointment.timeRange;

          for(let k = i; k < (i + itemRange); k++) {

            trackState.timeRange.push({
              operatorId: daysOutpatients[timeArr[i]].normalAppointment.operatorId,
              time: timeArr[k],
              left: daysOutpatients[timeArr[i]].normalAppointment.left,
              lastTimeSlot: (k + 1) === (i + itemRange) ? true : false,
            })
          }
        }
      }
    }
  }
}

