/**  */
const showCalendar = (year, month) => {
  let currentMonth = getCalendar(year, month, true);

  const firstWeekLen = currentMonth[0].length;
  const lastWeekLen = currentMonth[currentMonth.length - 1].length;

  if (firstWeekLen < 7 && firstWeekLen !== 0) {
    let lastMonth = getCalendar(year, month - 1, false);
    
    currentMonth[0] = lastMonth[lastMonth.length - 1].concat(currentMonth[0]);
  }
  
  if (lastWeekLen < 7 && lastWeekLen !== 0) {
    let nextMonth = getCalendar(year, month + 1, false);
    currentMonth[currentMonth.length - 1] = currentMonth[currentMonth.length - 1].concat(nextMonth[0]);
  }

  for(let i = 0; i < currentMonth.length; i++) {
    if (currentMonth[i].length === 0) {
      currentMonth.splice(i, 1);
    }
  }

  // 
  // if (!this.china) {
  //   this.dayLength = 6;
  //   for(let i = 0; i < currentMonth.length; i++) {
  //     currentMonth[i].splice(0, 1);
  //   }
  // }

  trackState.calendar = currentMonth;
  trackState.calendarData = currentMonth;
}

/**  */
const getCalendar = (year, month, j) => {
  let calendar = [];
  let week = [];
  let data = new Date(year, month + 1, 1);
 
  data.setHours(data.getHours() - 1);                //  - 1  

  const allDate = data.getDate();                    // : 1  31 

  for (let i = 1; i <= allDate; i++) {

    let morning = [];
    let afternoon = [];
    let evening = [];

    if (j) {
      for (let g = 0; g < trackState.showCalendarData.length; g++) {
        if (i === trackState.showCalendarData[g].day) {
          if (trackState.showCalendarData[g].clinicPeriod === 'Morning') {
            morning.push(trackState.showCalendarData[g]);
          }

          if (trackState.showCalendarData[g].clinicPeriod === 'Afternoon') {
            afternoon.push(trackState.showCalendarData[g]);
          }

          if (trackState.showCalendarData[g].clinicPeriod === 'Evening') {
            evening.push(trackState.showCalendarData[g]);
          }
        }
      }
    }

    let day = new Date(year, month, i).getDay(); //  : 0 - 6
      if (month === 12) {
        month = 0;
        year += 1;
      }

      if (month === -1) {
        month = 11;
        year -= 1;
      }

      week.push({
        year: year,
        month: month + 1,
        date: i,
        day: day,
        isCurrentMonth: j,
        morning,
        afternoon,
        evening,
      });

      if (day === 6 || i === allDate) {
        calendar.push(week);
        week = [];
      }
  }
  return calendar;
}

/**  */
const timeSwitch = (year, month, judge) => {
  if (judge) {
    month -= 1;
  } else {
    month += 1;
  }

  if (month === 12) {
    month = 0;
    year+= 1;
  }

  if (month === -1) {
    month = 11;
    year -= 1;
  }

  return { year, month };
}

