/** () */
async function getDepartmentFollowUpDataBySearchCondition(customer) {
  const {name, birthday, cellPhone, courseTypeId } = customer;
  const data = {
    customerName: name,
    customerBirthday: birthday,
    customerCellPhone: cellPhone,
    courseTypeId: courseTypeId,
  }

  const date = `${trackState.year}-${trackState.month + 1 <= 9 ?'0':''}${trackState.month + 1}-${trackState.date <= 9 ?'0':''}${trackState.date}`;

  let url = `/api/MedicalRecord/GetDepartmentFollowUpDataBySearchCondition?departmentId=${trackState.department}&date=${date}`;

  const token = fetchFunction.getTokenFromCookie();

  const options = {
    headers: {
      'Content-Type': 'application/json',
      "Authorization" : `Bearer ${token}`
    },
    method: 'GET'
  }

  const key = Object.keys(data);
  
  key.forEach(item => {
    if (data[item]) {
      url += `&${item}=${data[item]}`;
    }
  })
  
  const response = await fetchFunction.fetchApi(url, options);
  
  detailedInformationInit(response.data);
}

/**  */
function detailedInformationInit(response) {

  console.log(response);

  let doctorAttendanceData = response;
  const customerIdArr = [];
  const data = [];
  
  if (!doctorAttendanceData) return null;

  let morning = trackState.filterQueryStatus.morning ? '' : null;
  let afternoon = trackState.filterQueryStatus.afternoon ? '' : null;
  let evening = trackState.filterQueryStatus.evening ? '' : null;

  doctorAttendanceData = doctorAttendanceData.filter(item => {
    const clinicPeriodName = item.followUpMedicalRecord.clinicPeriodName;
    return clinicPeriodName === morning || clinicPeriodName === afternoon || clinicPeriodName === evening;
  });


  if (trackState.filterQueryStatus.courseTypeId) {
    doctorAttendanceData = doctorAttendanceData.filter(item => {
      const clinicPeriodName = item.followUpMedicalRecord.courseTypeId;
      return clinicPeriodName === trackState.filterQueryStatus.courseTypeId;
    });
  }

  doctorAttendanceData.forEach(item => {

    if (!customerIdArr.includes(item.followUpMedicalRecord.customerId)) {
      customerIdArr.push(item.followUpMedicalRecord.customerId);
      data.push({
          customer: {
            customerId: item.followUpMedicalRecord.customerId,
            beauticianNickname: item.followUpMedicalRecord.beauticianNickname,
            customerName: item.followUpMedicalRecord.customerName,
            customerBirthday: item.followUpMedicalRecord.customerBirthday,
            customerTraitColor: item.customerTraitColor.color,
          },
          followUpMedicalRecord: [],
        });
    }
  })

  doctorAttendanceData.forEach(itemA => {

    const detailItem = {
      cureContent: {
        ...itemA.followUpMedicalRecord,
      },
      voucherOrderDetails: [],
      bodyPart: [],
      beautyConsumableName: itemA.followUpMedicalRecord.beautyConsumableName,
      orderDetails: [],
      totalExpense: 0,
      medicalRecordSubcourseData: itemA.medicalRecordSubcourseData,
      medicalRecordCustomerDemands: itemA.medicalRecordCustomerDemands,
      beauticians: itemA.beauticians,
      suggestionOfToday: itemA.suggestionOfToday,
      doctorSigned: itemA.followUpMedicalRecord.doctorSigned,
      needToSignConsent: itemA.needToSignConsent,
      needToSignPhotoConsent: itemA.needToSignPhotoConsent,
      customerStoredValueData: itemA.customerStoredValueData
    }

    // 
    // 
    // 
    if (itemA.followUpMedicalRecord.anesthesiaSituation === "CancelAnesthesia") {
      detailItem.orderDetails.push({
        chargeType: 'anesthesia',
        chargeName: ' ()',
        anesthesiaSituation: "CancelAnesthesia",
        price: 0,
        received: true,
      })
    }

    // 
    if (itemA.followUpMedicalRecord.anesthesiaSituation === "Anesthesia") {
      detailItem.orderDetails.push({
        chargeType: 'anesthesia',
        chargeName: '',
        anesthesiaSituation: "Anesthesia",
        price: itemA.anestheticOrderDetail.price === 0 ? '' : itemA.anestheticOrderDetail.price,
        received: itemA.anestheticOrderDetail.received,
      })
    }

    const experienceData = {
      chargeType: 'cash',
      chargeName: '',
      bodyParts: [],
      price: [],
      received: [],
    }

    const depositData = {
      chargeType: 'cash',
      chargeName: '',
      bodyParts: [],
      price: [],
      received: [],
    }

    const onceData = {
      chargeType: 'cash',
      chargeName: '',
      bodyParts: [],
      price: [],
      received: [],
    }

    // 
    itemA.coursePartOrderDetails.forEach(itemB => {
      
      detailItem.totalExpense += parseInt(itemB.price);

      //  - 
      if (itemB.priceType === 'Experience') {
        let bodyPart = itemA.bodyPartOptions.find(itemC => itemC.id === itemB.bodyPartId);
        if (itemA.followUpMedicalRecord.courseTypeId === 'IV') {
          bodyPart = {
            name: 'IV',
          }
        }
        
        if (bodyPart.name === '') {
          experienceData.bodyParts.unshift(bodyPart);
          experienceData.price.unshift(itemB.price);
          experienceData.received.unshift(itemB.received);
          return null;
        }

        experienceData.bodyParts.push(bodyPart);
        experienceData.price.push(itemB.price);
        experienceData.received.push(itemB.received);
        return null;
      }

      //  - 
      if (itemB.priceType === 'Deposit') {
        let bodyPart = itemA.bodyPartOptions.find(itemC => itemC.id === itemB.bodyPartId);
        if (itemA.followUpMedicalRecord.courseTypeId === 'IV') {
          bodyPart = {
            name: 'IV',
          }
        }
        if (bodyPart.name === '') {
          depositData.bodyParts.unshift(bodyPart);
          depositData.price.unshift(itemB.price);
          depositData.received.unshift(itemB.received);
          return null;
        }

        depositData.bodyParts.push(bodyPart);
        depositData.price.push(itemB.price);
        depositData.received.push(itemB.received);
        return null;
      }

      //  - 
      if (itemB.priceType === 'Once') {
        if (itemB.bodyPartId === 'Other') {
          onceData.bodyParts.push({
            id: itemB.bodyPartId,
            name: itemB.otherPartName,
          });
          onceData.price.push(itemB.price);
          onceData.received.push(itemB.received);
          return null;
        }

        let bodyPart = itemA.bodyPartOptions.find(itemC => itemC.id === itemB.bodyPartId);
        if (itemA.followUpMedicalRecord.courseTypeId === 'IV') {
          bodyPart = {
            name: 'IV',
          }
        }

        if (bodyPart.name === '') {
          onceData.bodyParts.unshift(bodyPart);
          onceData.price.unshift(itemB.price);
          onceData.received.unshift(itemB.received);
          return null;
        }

        onceData.bodyParts.push(bodyPart);
        onceData.price.push(itemB.price);
        onceData.received.push(itemB.received);
        return null;
      }
    })

    if (experienceData.bodyParts.length !== 0) {
      detailItem.orderDetails.push(experienceData);
    }

    if (depositData.bodyParts.length !== 0) {
      detailItem.orderDetails.push(depositData);
    }

    if (onceData.bodyParts.length !== 0) {
      detailItem.orderDetails.push(onceData);
    }
    
    // detailItem.bodyPart 
    detailItem.bodyPart = itemA.bodyPartOptions.map(item => {
      const bodyPartItem = itemA.bodyParts.find(t => item.id === t.bodyPartId);

      if (bodyPartItem) {
        return {
          name: item.name,
          ...bodyPartItem
          // itemA
        }
      }
    })

    // detailItem.bodyPart = itemA.bodyParts.map(item1 => {
    //   const bodyPartItem = itemA.bodyPartOptions.find(item2 => item2.id === item1.bodyPartId);

    //   return {
    //     name: bodyPartItem.name,
    //     ...item1
    //   }
    // });

    const allCoupons = [
      ...itemA.voucherOrderDetails,
      ...itemA.couponOrderDetails,
      ...itemA.oweVoucherSituationData
    ];

    //  -  id 
    const uniqueId = allCoupons.filter((item, index, self) => {
      return index === self.findIndex((t) => t.bodyPartId === item.bodyPartId)
    })

    detailItem.voucherOrderDetails = uniqueId.map(item => {
      if (itemA.followUpMedicalRecord.courseTypeName === '') {
        return {
          bodyPartName: 'IV',
          bodyPartId: item.bodyPartId,
          voucherId: [],
        }
      }

      let bodyPartName = itemA.bodyPartOptions.find(itemB => itemB.id === item.bodyPartId);

      return {
        bodyPartName: bodyPartName ? bodyPartName.name : null,
        bodyPartId: item.bodyPartId,
        voucherId: [],
      }
    })

    //  bodyPartId
    itemA.oweVoucherSituationData.forEach(item1 => {

      detailItem.voucherOrderDetails.forEach(item2 => {

        if (item2.bodyPartId === item1.bodyPartId) {
          item2.voucherId.push('oweVoucher');

          return null;
        }
      })
    })

    //  bodyPartId
    itemA.voucherOrderDetails.forEach(item1 => {

      detailItem.voucherOrderDetails.forEach(item2 => {

        if (item2.bodyPartId === item1.bodyPartId) {
          item2.voucherId.push(item1.voucherId);

          return null;
        }
      })
    })

    //  bodyPartId
    itemA.couponOrderDetails.forEach(item1 => {

      let couponNumber = item1.couponNumber.toString().padStart(3, 0);

      let volumeNumber = `${item1.activityVoucherId}${couponNumber}`;

      detailItem.voucherOrderDetails.forEach(item2 => {

        if (item2.bodyPartId === item1.bodyPartId) {
          item2.voucherId.push(volumeNumber);
        }
      })
    })

    //  detailItem
    data.forEach(itemB => {
      if (itemB.customer.customerId === itemA.followUpMedicalRecord.customerId) {

        if (itemB.followUpMedicalRecord.length === 0) {
          itemB.followUpMedicalRecord.push({
            operatorName: itemA.followUpMedicalRecord.operatorName,
            operatorId: itemA.followUpMedicalRecord.operatorId,
            detail: [detailItem],
          })

          return null;
        }

        const judge = itemB.followUpMedicalRecord.some(itemC => itemC.operatorId === itemA.followUpMedicalRecord.operatorId);

        if (!judge) {
          itemB.followUpMedicalRecord.push({
              operatorName: itemA.followUpMedicalRecord.operatorName,
              operatorId: itemA.followUpMedicalRecord.operatorId,
              detail: [detailItem],
          })
        } else {
          itemB.followUpMedicalRecord.forEach(itemD => {
            if (itemD.operatorId === itemA.followUpMedicalRecord.operatorId) {
              itemD.detail.push(detailItem);
            }
          })
        }
      }
    })
  })

  // 

  console.log(data);
  
  // 
  data.forEach(itemA => {
    // 
    itemA.followUpMedicalRecord.forEach(itemB => {
      // 
      let arrive = [];
      let hasLeft = [];
      itemB.detail.forEach(itemC => {
        // 
        if (itemC.cureContent.arrivedTime && !itemC.cureContent.leftTime) {
          arrive.push(itemC);
        }

        if (itemC.cureContent.arrivedTime && itemC.cureContent.leftTime) {
          hasLeft.push(itemC);
        }
      })

      arrive.sort((a, b) => {
        const splitA = a.cureContent.arrivedTime.split('T');
        const splitB = b.cureContent.arrivedTime.split('T');
        let timeA = new Date(`${splitA[0]} ${splitA[1]}`).getTime();
        let timeB = new Date(`${splitB[0]} ${splitB[1]}`).getTime();

        if (timeA > timeB) {
          return -1;
        }

        if (timeA < timeB) {
          return 1;
        }

        if (timeA === timeB) {
          return 0;
        }
      });

      hasLeft.sort((a, b) => {
        const splitA = a.cureContent.arrivedTime.split('T');
        const splitB = b.cureContent.arrivedTime.split('T');
        let timeA = new Date(`${splitA[0]} ${splitA[1]}`).getTime();
        let timeB = new Date(`${splitB[0]} ${splitB[1]}`).getTime();

        if (timeA > timeB) {
          return 1;
        }

        if (timeA < timeB) {
          return -1;
        }

        if (timeA === timeB) {
          return 0;
        }
      });

      itemB.detail = arrive.concat(hasLeft);
    })
  })

  // console.log(itemB.detail);

  // 
  data.forEach(itemA => {
    // 
    let arrive = [];
    let hasLeft = [];
    itemA.followUpMedicalRecord.forEach(itemB => {
      // 

      if (itemB.detail[0].cureContent.arrivedTime && !itemB.detail[0].cureContent.leftTime) {
        arrive.push(itemB);
      }

      if (itemB.detail[0].cureContent.arrivedTime && itemB.detail[0].cureContent.leftTime) {
        hasLeft.push(itemB);
      }
    })

    arrive.sort((a, b) => {
      const splitA = a.detail[0].cureContent.arrivedTime.split('T');
      const splitB = b.detail[0].cureContent.arrivedTime.split('T');
      let timeA = new Date(`${splitA[0]} ${splitA[1]}`).getTime();
      let timeB = new Date(`${splitB[0]} ${splitB[1]}`).getTime();

      if (timeA > timeB) {
        return -1;
      }

      if (timeA < timeB) {
        return 1;
      }

      if (timeA === timeB) {
        return 0;
      }
    });

    hasLeft.sort((a, b) => {
      const splitA = a.detail[0].cureContent.arrivedTime.split('T');
      const splitB = b.detail[0].cureContent.arrivedTime.split('T');
      let timeA = new Date(`${splitA[0]} ${splitA[1]}`).getTime();
      let timeB = new Date(`${splitB[0]} ${splitB[1]}`).getTime();

      if (timeA > timeB) {
        return 1;
      }

      if (timeA < timeB) {
        return -1;
      }

      if (timeA === timeB) {
        return 0;
      }
    });
  
    itemA.followUpMedicalRecord = arrive.concat(hasLeft);
  })

  // 
  let arrive = [];
  let hasLeft = [];

  data.forEach(itemA => {
    // 
    if (itemA.followUpMedicalRecord[0].detail[0].cureContent.arrivedTime && !itemA.followUpMedicalRecord[0].detail[0].cureContent.leftTime) {
      arrive.push(itemA);
    }

    if (itemA.followUpMedicalRecord[0].detail[0].cureContent.arrivedTime && itemA.followUpMedicalRecord[0].detail[0].cureContent.leftTime) {
      hasLeft.push(itemA);
    }
  })

  arrive.sort((a, b) => {
    const splitA = a.followUpMedicalRecord[0].detail[0].cureContent.arrivedTime.split('T');
    const splitB = b.followUpMedicalRecord[0].detail[0].cureContent.arrivedTime.split('T');
    let timeA = new Date(`${splitA[0]} ${splitA[1]}`).getTime();
    let timeB = new Date(`${splitB[0]} ${splitB[1]}`).getTime();

    if (timeA > timeB) {
      return -1;
    }

    if (timeA < timeB) {
      return 1;
    }

    if (timeA === timeB) {
      return 0;
    }
  });

  hasLeft.sort((a, b) => {
    const splitA = a.followUpMedicalRecord[0].detail[0].cureContent.arrivedTime.split('T');
    const splitB = b.followUpMedicalRecord[0].detail[0].cureContent.arrivedTime.split('T');
    let timeA = new Date(`${splitA[0]} ${splitA[1]}`).getTime();
    let timeB = new Date(`${splitB[0]} ${splitB[1]}`).getTime();

    if (timeA > timeB) {
      return 1;
    }

    if (timeA < timeB) {
      return -1;
    }

    if (timeA === timeB) {
      return 0;
    }
  });

  if (trackState.trackContent === 'detailedInformation') {
    detailedTrackInformationData.value = hasLeft;
    return null;
  }

  // 
  const arrived = [];
  const endTime = [];

  arrive.forEach(doc => {
    const alljudge = [];

    doc.followUpMedicalRecord.forEach(customer => {
      const endTimeJudge = customer.detail.some(item => item.cureContent.endTimeOfTreatment);

      if (endTimeJudge) {
        alljudge.push(customer);
      }
    })

    if (alljudge.length === 0) {
      arrived.push(doc);
    } else {
      endTime.push(doc);
    }
  })

  detailedTrackInformationData.value = [...arrived, ...endTime, ...hasLeft];
}

