const continuousOrReservedToggle = async() => {

  if (trackState.department === 'AestheticMedicine') {
    resetSelectedDoctor();

    if (trackState.trackContent === 'dateOfAppointment') await getDoctorFollowUpData();

    if (trackState.trackContent === 'detailedInformation') await getDoctorFollowUpData();
  }

  if (trackState.department === 'Beauty') {
  
    if (trackState.trackContent === 'dateOfAppointment') await getDepartmentFollowUpData();

    if (trackState.trackContent === 'detailedInformation') await getDepartmentFollowUpData();
  }

  getClinicPeriodLastTime();
}


